import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SharedModule } from '../../shared/shared.module';
import { NgxPaginationModule } from 'ngx-pagination';
import { CommonPopUpModule } from '../commonPopUp/commonPopUp.module';

import { ConsultaStockComponent } from './consultaStock/consultaStock.component';
import { BuscarArticuloPopUpComponent } from '../commonPopUp/buscarArticuloPopUp/buscarArticuloPopUp.component';
import { LineaPopUpComponent } from '../commonPopUp/lineaPopUp/lineaPopUp.component';
import { SucursalesPopUpComponent } from '../commonPopUp/sucursalesPopUp/sucursalesPopUp.component';
import { BodegaPopUpComponent } from '../commonPopUp/bodegaPopUp/bodegaPopUp.component';
import { BuscaOrdenesDeCompraComponent } from './buscaOrdenesDeCompra/buscaOrdenesDeCompra.component';
import { SalidaVariaComponent } from './salidaVaria/salidaVaria.component';
import { DetalleSalidaVariaComponent } from './detalleSalidaVaria/detalleSalidaVaria.component';

import { DetalleOrdenDeCompraComponent } from './detalleOrdenDeCompra/detalleOrdenDeCompra.component';
import { MovimientosDiariosComponent } from './movimientosDiarios/movimientosDiarios.component';
import { AbastecimientoComponent } from './abastecimiento/abastecimiento.component';
import { AbastecimientoDetalleOCComponent } from './abastecimiento/abastecimientoDetalleOC/abastecimientoDetalleOC.component';


const routes: Routes = [
	{ path: 'consultaStock', component: ConsultaStockComponent },
	{ path: 'buscaOrdenesDeCompra', component: BuscaOrdenesDeCompraComponent },
	{ path: 'detalleOrdenDeCompra', component: DetalleOrdenDeCompraComponent },
	{ path: 'salidaVaria', component: SalidaVariaComponent },
	{ path: 'detalleSalidaVaria', component: DetalleSalidaVariaComponent },
	{ path: 'movimientosDiariosStock', component: MovimientosDiariosComponent },
	{ path: 'abastecimiento', component: AbastecimientoComponent },
	{ path: 'abastecimientoDetalleOC', component: AbastecimientoDetalleOCComponent }

];


@NgModule({
	imports: [
		SharedModule,
		RouterModule.forChild(routes),
		CommonModule,
		NgxPaginationModule,
		CommonPopUpModule
	],
	declarations: [
		ConsultaStockComponent,
		BuscaOrdenesDeCompraComponent,
		DetalleOrdenDeCompraComponent,
		SalidaVariaComponent,
		DetalleSalidaVariaComponent,
		MovimientosDiariosComponent,
		AbastecimientoComponent,
		AbastecimientoDetalleOCComponent],

	entryComponents: [
		BuscarArticuloPopUpComponent,
		SucursalesPopUpComponent,
		BodegaPopUpComponent]
})

export class ExistenciasModule { }
