import { Component, OnInit, OnDestroy, ViewChild, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { registerLocaleData, Time } from '@angular/common';
import es from '@angular/common/locales/es';

import { NgxSpinnerService } from 'ngx-spinner';
import * as _ from 'lodash';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

import { AdministracionComercialService } from '../../../core/consultaService/administracionComercial.service';
import { UtilService } from '../../../services/util.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { ExistenciasService } from '../../../core/consultaService/existencias.service';

const swal = require('sweetalert');

@Component({
  selector: 'app-salidaVaria',
  templateUrl: './salidaVaria.component.html'
})
export class SalidaVariaComponent implements OnInit, OnDestroy {

  pageActual = 1;
  forma: FormGroup;
  fechaActual = new Date();
  bsModalRef: BsModalRef;
  listadoSalidasVarias: Array<any> = [];
  user: any = {};
  suscripciones: Array<any> = [];

  tiposMovimiento: Array<any> = [];

  estadosNuevo: Array<any> = [
    {
      codigoEstado: 'PAPRO',
      estado: 'PENDIENTE APROBACION'
    },
    {
      codigoEstado: 'APROB',
      estado: 'APROBADA'
    },
    {
      codigoEstado: 'RECHA',
      estado: 'RECHAZADA'
    },
    {
      codigoEstado: 'NUEVO',
      estado: 'ESTADO NUEVO'
    }
  ];

  estados: Array<any> = [
    {
      codigoEstado: 'PAPRO',
      estado: 'PENDIENTE APROBACION'
    },
    {
      codigoEstado: 'APROB',
      estado: 'APROBADA'
    },
    {
      codigoEstado: 'RECHA',
      estado: 'RECHAZADA'
    }
  ];

  @ViewChild('montoInversion') montoInversion: ElementRef;

  constructor(private fb: FormBuilder,
    private spinner: NgxSpinnerService,
    private utilService: UtilService,
    private administracionComercialService: AdministracionComercialService,
    private router: Router,
    private authService: AuthService,
    private modalService: BsModalService,
    private existenciasService: ExistenciasService) {
    this.forma = new FormGroup({
      salidaVaria: new FormControl(null),
      fechaDesde: new FormControl('', Validators.required),
      fechaHasta: new FormControl('', Validators.required),
      codTipoMovimiento: new FormControl(''),
      codEstado: new FormControl('')
    });
  }

  ngOnInit() {

    this.estadosNuevo.forEach(estadoNuevo => {
      let _existe: any;
      _existe = this.estados.find(estado => estado.codigoEstado !== estadoNuevo.codigoEstado);
      console.log(_existe);
    });

    this.cargarInformacionInicial();
  }

  cargarInformacionInicial() {
    registerLocaleData(es);
    this.user = this.authService.getDatosUser();
    this.tiposMovimiento = this.authService.getParametrosTipoMovimiento();
    this.forma.controls.fechaDesde.setValue(this.utilService.dateToStringFormat(this.fechaActual));
    this.forma.controls.fechaHasta.setValue(this.utilService.dateToStringFormat(this.fechaActual));
  }

  listarSalidasVarias() {
    if (this.validarEntradasFormulario()) {
      this.spinner.show();

      const request = {
        nRutOperador: this.user.nrutoperador,
        nSalidaVaria: this.forma.controls.salidaVaria.value === null ? 0 : this.forma.controls.salidaVaria.value,
        sCodEstado: this.forma.controls.codEstado.value,
        sCodTipoMovimiento: this.forma.controls.codTipoMovimiento.value,
        sFechaDesde: (this.forma.controls.fechaDesde.value).replace(/-/g, ''),
        sFechaHasta: (this.forma.controls.fechaHasta.value).replace(/-/g, '')
      };

      this.suscripciones.push(this.existenciasService.listarSalidasVarias(request)
        .subscribe((salidasVarias: any) => {
          this.listadoSalidasVarias = salidasVarias.salidasVarias;
          if (salidasVarias.error.codError !== 0) {
            this.spinner.hide();
            swal('Atencin', 'Ha ocurrido un error inesperado!', salidasVarias.error);
          }
          if (salidasVarias.salidasVarias.length === 0) {
            this.spinner.hide();
            swal('Atencin', 'No se encontraron coincidencias', 'info');
          } else {
            this.listadoSalidasVarias.forEach((salidaVarias, i) => {
              this.listadoSalidasVarias[i]['sal_sdesestado'] =
                this.estados.find(estado => estado.codigoEstado === salidaVarias.sal_scodestado).estado;
            });
          }
          this.spinner.hide();
        },
          error => {
            this.spinner.hide();
            swal('Atencin', 'Ha ocurrido un error inesperado!', error);
          }));
    }
  }

  inicializarFechaDesdeInvalida() {
    if (this.forma.controls.fechaDesde.value === '') {
      this.forma.controls.fechaDesde.setValue(this.utilService.dateToStringFormat(this.fechaActual));
    }

    if (!this.utilService.DateValidator(this.forma.controls.fechaDesde.value)) {
      this.forma.controls.fechaDesde.setValue(this.utilService.dateToStringFormat(this.fechaActual));
    }
  }

  inicializarFechaHastaInvalida() {
    if (this.forma.controls.fechaHasta.value === '') {
      this.forma.controls.fechaHasta.setValue(this.utilService.dateToStringFormat(this.fechaActual));
    }

    if (!this.utilService.DateValidator(this.forma.controls.fechaHasta.value)) {
      this.forma.controls.fechaHasta.setValue(this.utilService.dateToStringFormat(this.fechaActual));
    }
  }

  redirigirDetalleSalidaVaria(salidaSeleccionada: any | null) {
    if (salidaSeleccionada.sal_sdesestado === 'APROBADA' || salidaSeleccionada.sal_sdesestado === 'RECHAZADA') {
      this.router.navigate(['/existencias/detalleSalidaVaria']);
    }
  }

  validarEntradasFormulario(): boolean {
    let _validarEntradasFormulario = true;
    if (new Date(this.forma.controls.fechaHasta.value) < new Date(this.forma.controls.fechaDesde.value)) {
      swal('Atencin', 'Fecha Hasta debe ser mayor a la Fecha Desde!', 'error');
      _validarEntradasFormulario = false;
    }
    return _validarEntradasFormulario;
  }

  desplegarPopUp(salida: any) {

  }

  ngOnDestroy(): void {
    this.suscripciones.forEach(suscripcion => suscripcion.unsubscribe());
  }
}
