import { Component, OnInit, OnDestroy } from '@angular/core';
import { registerLocaleData, Time } from '@angular/common';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';

import { ExistenciasService } from '../../../core/consultaService/existencias.service';
import { AuthService } from '../../../core/consultaService/auth.service';
import { ExcelService } from '../../../core/excel/excel.service';
import { UtilService } from '../../../services/util.service';
import { ConsultasService } from './../../../core/consultaService/consultas.service';
import { ArticuloService } from '../../../core/consultaService/articulo.service';
import { AdministracionOperativaService } from '../../../core/consultaService/administracionOperativa.service';


import { NgxSpinnerService } from 'ngx-spinner';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { SucursalesPopUpComponent } from '../../commonPopUp/sucursalesPopUp/sucursalesPopUp.component';
import { BuscarArticuloPopUpComponent } from '../../commonPopUp/buscarArticuloPopUp/buscarArticuloPopUp.component';
import es from '@angular/common/locales/es';
import { BodegaPopUpComponent } from '../../commonPopUp/bodegaPopUp/bodegaPopUp.component';
declare var $: any;
const swal = require('sweetalert');

@Component({
	selector: 'app-consultaStock',
	templateUrl: './consultaStock.component.html',
	styleUrls: ['./consultaStock.component.scss']
})

export class ConsultaStockComponent implements OnInit, OnDestroy {

	fechaActual = new Date();

	forma: FormGroup;

	pageActual = 1;

	request: any = {};

	user: any = {};

	stockArticulos: Array<any> = [];

	origen: string = '';

	bsModalRef: BsModalRef;

	subData: any;

	sucursalModal: BsModalRef;

	exportar = false;

	datos: Array<any> = [];
	

	constructor(private existenciasService: ExistenciasService,
		public cargador: NgxSpinnerService, private authService: AuthService, private excelService: ExcelService,
		private utilService: UtilService, private consultasService: ConsultasService, private modalService: BsModalService,
		private articuloService: ArticuloService,
		 private administracionOperativaServiceDatos: AdministracionOperativaService) {

		this.forma = new FormGroup({
			codLinea: new FormControl(''),
			descLinea: new FormControl({ value: '', disabled: true }, Validators.required),
			codCategoria: new FormControl(''),
			descCategoria: new FormControl({ value: '', disabled: true }, Validators.required),
			codAgrupacion: new FormControl(''),
			descAgrupacion: new FormControl({ value: '', disabled: true }, Validators.required),
			codProdBase: new FormControl(''),
			descProdBase: new FormControl({ value: '', disabled: true }, Validators.required),
			codArticulo: new FormControl(''),
			codArtVariedad: new FormControl(''),
			codSucursal: new FormControl(''),
			descSucursal: new FormControl({ value: '', disabled: true }, Validators.required),
			codBodega: new FormControl(''),
			descBodega: new FormControl({ value: '', disabled: true }, Validators.required),
			estado: new FormControl(),
			composicion: new FormControl(),
			descriptor: new FormControl('')
		});
	}

	ngOnInit() {
		this.user = this.authService.getDatosUser();
		// console.log('user Stock ' + this.user.nrutoperador);
		registerLocaleData(es);
		this.subData = this.consultasService.datos$.subscribe(resData => {
			this.setData(resData);

		})
	}
	ngOnDestroy() {
		if (this.subData !== undefined) {
			this.subData.unsubscribe();
		}

	}


	validaLinea() {
		if (this.forma.controls.codLinea.value === '') {
			this.forma.controls.codLinea.setValue('');
			this.forma.controls.descLinea.setValue('');
		} else {
			this.request.scodestado = 'VIGEN';
			this.request.scodlinea = this.forma.controls.codLinea.value;
			this.cargador.show();
			this.articuloService.getLineas(this.request)
				.subscribe((data: any) => {
					this.cargador.hide();
					if (data) {
						if (data.error.codError != 0) {
							swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
						} else {
							this.datos = data.articulo;
							if (this.datos.length > 0) {
								this.forma.controls.descLinea.setValue(this.datos[0].jli_sdeslinea);
							} else {
								this.forma.controls.codLinea.setValue('');
								this.forma.controls.descLinea.setValue('');
								swal('Atencin', 'No Existe Lnea', 'warning');
							}
						}
					}
				},
					error => {
						this.cargador.hide();
						swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
					})
		}
	}

	validaCategoria() {
		if (this.forma.controls.codCategoria.value === '') {
			this.forma.controls.codCategoria.setValue('');
			this.forma.controls.descCategoria.setValue('');
		} else {
			this.request.scodestado = 'VIGEN';
			this.request.scodcategoria = this.forma.controls.codCategoria.value;
			this.cargador.show();
			this.articuloService.getCategorias(this.request)
				.subscribe((data: any) => {
					this.cargador.hide();
					if (data) {
						if (data.error.codError != 0) {
							swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
						} else {
							this.datos = data.articulo;
							if (this.datos.length > 0) {
								this.forma.controls.descCategoria.setValue(this.datos[0].jca_sdescategoria);
							} else {
								this.forma.controls.codCategoria.setValue('');
								this.forma.controls.descCategoria.setValue('');
								swal('Atencin', 'No Existe Categora', 'warning');
							}
						}
					}
				},
					error => {
						this.cargador.hide();
						swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
					})
		}
	}

	validaAgrupacion() {
		if (this.forma.controls.codAgrupacion.value === '') {
			this.forma.controls.codAgrupacion.setValue('');
			this.forma.controls.descAgrupacion.setValue('');
		} else {
			this.request.scodestado = 'VIGEN';
			this.request.scodagrupacion = this.forma.controls.codAgrupacion.value;
			this.cargador.show();
			this.articuloService.getAgrupacion(this.request)
				.subscribe((data: any) => {
					this.cargador.hide();
					if (data) {
						if (data.error.codError != 0) {
							swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
						} else {
							this.datos = data.articulo;
							if (this.datos.length > 0) {
								this.forma.controls.descAgrupacion.setValue(this.datos[0].jag_sdesagrupacion);
							} else {
								this.forma.controls.codAgrupacion.setValue('');
								this.forma.controls.descAgrupacion.setValue('');
								swal('Atencin', 'No Existe Agrupacin', 'warning');
							}
						}
					}
				},
					error => {
						this.cargador.hide();
						swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
					})
		}
	}

	validaProdBase() {
		if (this.forma.controls.codProdBase.value === '') {
			this.forma.controls.codProdBase.setValue('');
			this.forma.controls.descProdBase.setValue('');
		} else {
			this.request.scodestado = 'VIGEN';
			this.request.scodproductobase = this.forma.controls.codProdBase.value;
			this.cargador.show();
			this.articuloService.getProdBase(this.request)
				.subscribe((data: any) => {
					this.cargador.hide();
					if (data) {
						if (data.error.codError != 0) {
							swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
						} else {
							this.datos = data.articulo;
							if (this.datos.length > 0) {
								this.forma.controls.descProdBase.setValue(this.datos[0].jpb_sdesproductobase);
							} else {
								this.forma.controls.codProdBase.setValue('');
								this.forma.controls.descProdBase.setValue('');
								swal('Atencin', 'No Existe Producto Base', 'warning');
							}
						}
					}
				},
					error => {
						this.cargador.hide();
						swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
					})
		}
	}
	validaBodega() {
		if (this.forma.controls.codBodega.value === '') {
			this.forma.controls.codBodega.setValue('');
			this.forma.controls.descBodega.setValue('');
		} else {
			this.request.runOperador= this.user.nrutoperador;
			this.request.sCodBodega = this.forma.controls.codBodega.value;
			this.cargador.show();
			this.administracionOperativaServiceDatos.getBodegas(this.request)
				.subscribe((data: any) => {
					this.cargador.hide();
					if (data) {
						if (data.error.codError != 0) {
							swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
						} else {
							this.datos = data.bodegas;
							if (this.datos.length > 0) {
								this.forma.controls.descBodega.setValue(this.datos[0].bod_sdescbodega);
							} else {
								this.forma.controls.codBodega.setValue('');
								this.forma.controls.descBodega.setValue('');
								swal('Atencin', 'No Existe Bodega', 'warning');
							}
						}
					}
				},
					error => {
						this.cargador.hide();
						swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
					})
		}
	}
	
	validaSucursal() {
		if (this.forma.controls.codSucursal.value === '') {
			this.forma.controls.codSucursal.setValue('');
			this.forma.controls.descSucursal.setValue('');
		} else {
			this.request.nRutOperador= this.user.nrutoperador;
			this.request.sCodSucursal = this.forma.controls.codSucursal.value;
			console.log(this.request);
			this.cargador.show();
			this.administracionOperativaServiceDatos.getSucursales(this.request)
				.subscribe((data: any) => {
					this.cargador.hide();
					if (data) {
						if (data.error.codError != 0) {
							swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
						} else {
							this.datos = data.sucursalExistencia;
							if (this.datos.length > 0) {
								this.forma.controls.descSucursal.setValue(this.datos[0].suc_sdireccion);
							} else {
								this.forma.controls.codSucursal.setValue('');
								this.forma.controls.descSucursal.setValue('');
								swal('Atencin', 'No Existe Sucursal', 'warning');
							}
						}
					}
				},
					error => {
						this.cargador.hide();
						swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
					})
		}
	}
	openModalLineas() {
		this.origen = 'linea';
		const initialState = {
			titulo: 'Lneas',
			origen: 'linea'
		};
		this.bsModalRef = this.modalService.show(BuscarArticuloPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
	}

	openModalCategorias() {
		this.origen = 'categoria';
		const initialState = {
			titulo: 'Categoras',
			origen: 'categoria'
		};
		this.bsModalRef = this.modalService.show(BuscarArticuloPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
	}

	openModalAgrupacion() {
		this.origen = 'agrupacion';
		const initialState = {
			titulo: 'Agrupaciones',
			origen: 'agrupacion'
		};
		this.bsModalRef = this.modalService.show(BuscarArticuloPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
	}

	openModalProdBase() {
		this.origen = 'prodbase';
		const initialState = {
			titulo: 'Productos Base',
			origen: 'prodbase'
		};
		this.bsModalRef = this.modalService.show(BuscarArticuloPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
	}

	openModalSucursales() {
		console.log("entra al modal sucursal");
		this.origen = 'sucursales';
		const initialState = {
			titulo: 'Sucursales',
			origen: 'sucursales'
		};
		this.bsModalRef = this.modalService.show(SucursalesPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
	}

	openModalBodega() {
		this.origen = 'bodega';
		const initialState = {
			titulo: 'Bodegas',
			origen: 'bodega'
		};
		this.bsModalRef = this.modalService.show(BodegaPopUpComponent, { initialState, backdrop: 'static', keyboard: false });
	}

	setData(resData) {
		if (resData != 0) {
			switch (this.origen) {
				case 'linea': {
					this.forma.controls.codLinea.setValue(resData.jli_scodlinea);
					this.forma.controls.descLinea.setValue(resData.jli_sdeslinea);
					break;
				}
				case 'categoria': {
					this.forma.controls.codCategoria.setValue(resData.jca_scodcategoria);
					this.forma.controls.descCategoria.setValue(resData.jca_sdescategoria);
					break;
				}
				case 'agrupacion': {
					this.forma.controls.codAgrupacion.setValue(resData.jag_scodagrupacion);
					this.forma.controls.descAgrupacion.setValue(resData.jag_sdesagrupacion);
					break;
				}
				case 'prodbase': {
					this.forma.controls.codProdBase.setValue(resData.jpb_scodproductobase);
					this.forma.controls.descProdBase.setValue(resData.jpb_sdesproductobase);
					break;
				}
				case 'sucursales': {
					console.log("entra al case");
					console.log(resData.suc_scodsucursal);
					this.forma.controls.codSucursal.setValue(resData.suc_scodsucursal);
					this.forma.controls.descSucursal.setValue(resData.suc_sdireccion);
					break;
				}
				case 'bodega': {
					console.log("entra al case bodega");
					this.forma.controls.codBodega.setValue(resData.bod_scodbodega);
					this.forma.controls.descBodega.setValue(resData.bod_sdescbodega);
					break;
				}
			}
		}
		$('body').removeClass('modal-open');
	}

	buscar($event: any) {
		this.exportar = false;
		this.stockArticulos = [];
		console.log('entra al metodo');
		this.request.nRutOperador = this.user.nrutoperador;
		console.log(this.request.nRutOperador);
		this.request.sCodAgrupacion = this.forma.controls.codAgrupacion.value;
		this.request.sCodBodega = this.forma.controls.codBodega.value;
		this.request.sCodCategoria = this.forma.controls.codCategoria.value;
		this.request.sCodFormato = this.forma.controls.codArticulo.value;
		this.request.sCodLinea = this.forma.controls.codLinea.value;
		this.request.sCodProductoBase = this.forma.controls.codProdBase.value;
		this.request.sCodVariedad = this.forma.controls.codArtVariedad.value;
		this.existenciasService.getArticulosExistencia(this.request)
			.subscribe((data: any) => {
				this.stockArticulos = data.articulosExistencia;
				console.log('data Service' + this.stockArticulos);
				this.exportar = true;
			}),
			error => {
				console.log('error en el request');
			}
	}

	generateExcel() {
		console.log('entra al metodo generarExel');

		let headerExcel = [
			"Lnea", "Categora", "Agrupacin", "Prod. Base", "Cod. Articulo", "Medidas", " Disponible U.v - Frac.",
			"Reservado U.v - Frac.", "Mal Estado U.v - Frac.", "Total U.v - Frac."];
		let dataExcel = [];
		for (let i in this.stockArticulos) {
			let articulo = [
				this.stockArticulos[i].jli_sdeslinea,
				this.stockArticulos[i].jca_sdescategoria,
				this.stockArticulos[i].jag_sdesagrupacion,
				this.stockArticulos[i].jpb_sdesproductobase,
				`${this.stockArticulos[i].art_scodformato} - ${this.stockArticulos[i].art_scodvariedad}`,
				this.stockArticulos[i].art_smedida,
				`${this.stockArticulos[i].uventadisponibles} - ${this.stockArticulos[i].fraccionesdisponibles}`,
				`${this.stockArticulos[i].uventareserva} - ${this.stockArticulos[i].fraccionesreserva}`,
				`${this.stockArticulos[i].uvtamalestado} - ${this.stockArticulos[i].fraccionesvtamalestado}`,
				`${this.stockArticulos[i].uvtatotales} - ${this.stockArticulos[i].fraccionestotales}`,
			]
			dataExcel.push(articulo);
		}
		let fechaExcel = this.utilService.dateToLocalStringFormat(this.fechaActual);
		this.excelService.generateExcel('Listado de Artculos', headerExcel, dataExcel, fechaExcel);
		swal('Perfecto', 'Su reporte ha sido generado con exito!', 'success');
	}
}
