  validaArticulo(articulo, descripcion, cantidad) {
    let articuloExiste = false;
    if (articulo.value !== '') {
      for (let i in this.parametrosAutocomplete) {
        let str = `${this.parametrosAutocomplete[i].art_scodformato}-${this.parametrosAutocomplete[i].art_scodvariedad}`;
        if (str.startsWith(`${articulo.value}`)) {
          articulo.value = str;
          descripcion.value = this.parametrosAutocomplete[i].art_sdescripcion;
          descripcion.focus();
          articuloExiste = true;
          this.verListaArticulo = false;
          // cantidad.focus();
          break;
        }
      }
      if (!articuloExiste) {
        articulo.value = '';
        descripcion.value = '';
        articulo.focus();
        swal('Atencin', 'Artculo no existe', 'warning');
      }
    } else {
      articulo.focus();
      swal('Atencin', 'Debe ingresar el Artculo', 'warning');
    }
  }

